

html, body, #root, .App {
  width: 100%;
  height: 100%;
  background-color: lightgray;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.App {
  font-size: 14px;
}

.App .live-only {
  display: none;
}

.App.live .live-only {
  display: block;
}

.download-header {
  text-align: center;
  margin: 20px;
}



/* Grid */

.cv-pdf {

  height: 792px;
  width: 612px;
  background-color: white;
  margin: 0 auto;
  padding: 36px 36px 40px 36px;

}

.App.live .cv-pdf {
  margin-top: 20px;
}

.grid-template {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 50px 1fr;
  grid-template-areas: 'contact contact' 'col-one col-two';
  grid-gap: 24px;
}

.contact {
  grid-area: contact;
}

.contact span {
  font-size: 10px;
  font-weight: bold;
}

.contact > p {
  margin-top: 0;
}

.col-one {
  grid-area: col-one;
  max-width: 100%;
}


.col-two {
  grid-area: col-two;
}

 .cv-pdf:nth-of-type(2) {
    padding-top: 56px;
  }

 .App.live .cv-pdf:nth-of-type(2) {
    padding-top: 36px;
  }

@media screen and (max-width: 612px) {
  .App.live .grid-template {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto;
    grid-template-areas: 'contact' 'col-one' 'col-two';
  }

  .App.live .cv-pdf {
    height: auto;
    width: auto;
    margin-top: 0;
  }

  .App.live .cv-pdf:nth-of-type(2) {
    padding-top: 0px;
  }

  .App.live .cv-pdf:nth-of-type(1) {
    padding-bottom: 0px;
  }

  .App.live .live-only-large {
    display: none;
  }
}

/* end of grid */
h2, h3 {
  margin-top: 0;
}

a {
  text-decoration: none;
  color: orangered;
  font-weight: bold;
}

section {
  margin-bottom: 36px;
}

ul {
  list-style: none;
  padding-left: 0;
}

.SectionTitle {
  font-size: 10px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.SectionTitle.date {
  color: darkgray;
  display: inline;
}

.SectionText.intro {
  font-weight: bold;
  margin-top: 0;
}

