  font-weight: bold;
  font-size: 10px;
}*/
.contact-ul a i {
  font-size: 18px;
}

.contact-ul li {
  /*line-height: 22px;*/
}

.inline-circle-img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  float: left;
  shape-outside: circle();
  margin-right: 24px;
  margin-bottom: 0px;
  transform: rotate(90deg);

}

.ProjectTitle * {
  font-size: 14px;
}

.App span {
  display: inline-block;
  margin-right: 8px;
}




